	module.exports = {"data":{"markdownRemark":{"html":"<h1>Виртуальная память</h1>\n<p>В системе с виртуальной памятью используемые программами адреса, называемые виртуальными адресами, транслируются в физические адреса памяти компьютера. Эту трансляцию выполняет специальный блок процессора или чипсета, называемый <strong>MMU (memory management unit)</strong>. Для программы основная память выглядит как непрерывное адресное пространство вне зависимости от наличия у компьютера соответствующего объема оперативной памяти. Соотнесением физический и виртуальной памяти, перемещением фрагментов между основным и вторичным хранилищами занимается операционная система.</p>\n<h2>Основные термины</h2>\n<ul>\n<li><strong>Реальный режим работы процессора</strong> - Режим работы процессоров, когда нет виртуальной памяти, а любому процессу доступна вся память компьютера. До появления процессоров 80286 это был единственный режим работы.</li>\n<li><strong>Защищенный режим работы процессора</strong> - Режим работы, использующий механизм виртуальной адресации памяти. </li>\n<li><strong>Своп/подкачка страниц</strong> - механизм виртуальной памяти, когда отдельные неактивные фрагменты памяти перемещаются из ОЗУ на диск, освобождая ОЗУ для загрузки других активных фрагментов памяти. Такими фрагментами являются страницы памяти.</li>\n<li><strong>Страничная память</strong> - Способ организации виртуальной памяти, при котором единицей отображения виртуальных адресов на физические является регион постоянного размера - страница. Типичный размер страницы - 4 КБ.</li>\n</ul>\n<p>Страницы памяти являются минимальной единицей выделяемой памяти, то есть даже запрос на 1 байт от приложения приведет к выделению ему страницы памяти. Пользовательский поток обращается к памяти с помощью адреса виртуальной памяти, который делится на номер страницы и смещение внутри страницы.</p>\n<h2>Принцип работы</h2>\n<p>Процессор преобразует номер виртуальной страницы в адрес соответствующей ей физической страницы при помощи буфера <strong>TLB (translation look-aside buffer)</strong>. </p>\n<p>Если это сделать не удалось, то нужно дозаполнить буфер, обратившись к таблице страниц (т.н. Page Walk), что делает либо сам процессор, либо ОС. </p>\n<p>Если страницы нет в оперативной памяти, то выстреливает событие Page Fault и в процессе его обработки ОС подкачивает страницу с жесткого диска. </p>\n<p>При запросе на выделение памяти ОС может \"сбросить\" на диск страницы, к которым давно не было обращений.</p>\n<p>Критические данные, такие как код и память ядра или код запущенных программ, обычно не свопятся и всегда находятся в оперативной памяти.</p>\n<h2>Преобразование адреса</h2>\n<p>Сначала логический адрес преобразовывается в линейный.</p>","frontmatter":{"path":"/blog/hardware/virtual-memory","title":"virtual-memory"}}},"pathContext":{}}

